<template>
  <b-col class="word-item" :class="{'checked': word.checked}">
    <div>
    <div class="toolbar">
        <fa-icon icon="language" size="lg" @click="openTranslate(word.word)" />
        <fa-icon icon="check-circle" class="float-right checked-icon" size="lg" @click="checkWord(word)" />
        <fa-icon icon="copy" class="float-right" size="lg" @click="copyWord(word)" />
    </div><!-- toolbar -->
        
      <h4>{{word.word}}</h4>
      <div class="word-content" :class="{'fullheight': open}" @dblclick="open = !open">
        <em>{{word.pronounce}}</em>
        <div v-for="(meanning, index) in word.meanings" :key="index">
          <strong v-if="meanning.startsWith('*')">{{meanning}}</strong>
          <span v-else>{{meanning}}</span>
        </div>
      </div>
    </div>
  </b-col>
  <!-- end .word-item -->
</template>

<script>
import BrowserHelper from 'app/service/browser_helper';
import Storage from 'app/service/storage'

export default {
  props: ["word"],
   data() {
    return {
      open: false,
    };
  },
  methods: {
    openTranslate(word){
        let link = `https://translate.google.com.vn/m/translate#view=home&op=translate&sl=en&tl=vi&text=${word}`;
        window.open(link)
    },
    checkWord(word) {
        word.checked = !word.checked;
        word.checked ?  Storage.checkWord(word.idx) : Storage.uncheckWord(word.idx)
        this.$forceUpdate();
    },
    copyWord(word) {
        BrowserHelper.copy(word.word);
    }
  }
};
</script>

<style scoped lang="scss">
.word-item {
  & {
    border: 1px solid #ddd;
    text-align: left;
    padding: 5px;
  }
  &.checked {
    border: 1px solid red;
    background-color: rgba(0, 0, 0, 0.1);

    .checked-icon{
        color: green;
    }
  }
  
  h4 {
    text-align: center;
  }

  .word-content {
    max-height: 300px;
    padding-bottom: 10px;
    overflow: hidden;
  }

  .word-content.fullheight {
    max-height: fit-content;
  }
}
 .toolbar>*{
     margin-left: 3px;
 }
</style>
