import remove from 'lodash/remove';

let instance = {};


let storage = window.localStorage;

const CHECK_LIST = 'CHECK_LIST';

let checklist = JSON.parse(storage.getItem(CHECK_LIST)) || []; // 


/*
const LAST_RHYME = 'LAST_RHYME'
instance.getLastRhyme = () => storage.getItem(LAST_RHYME);

instance.setLastRhyme = function(input){
    storage.setItem(LAST_RHYME, input)
}
*/

instance.isChecked = function(wordId) {
    return checklist.includes(wordId);
}

instance.checkWord = function(wordId){
    checklist.push(wordId);
    storage.setItem(CHECK_LIST, JSON.stringify(checklist));
}

instance.uncheckWord = function(wordId) {
    checklist = remove(checklist, function(wId){
        return wordId == wId;
    })
    storage.setItem(CHECK_LIST, JSON.stringify(checklist));
}

export default instance;