import axios from 'axios';
import last  from 'lodash/last';
import Storage from './storage';

var apiInstance = axios.create({
    baseURL: window.siteUrl
});

let instance = {}

instance.searchByRhyme = async function(rhyme){
    let data = await apiInstance.get('/search_rhyme/' + rhyme);

    data.data.map(function(item){
        let pieces = item.detail.split('<br />');

        /// format pronounce
        let pronounce = pieces.shift();
        item.pronounce = pronounce.split('@')[1];
        
        //  format last meanning
        let lastMeaning = last(pieces);
        pieces[pieces.length - 1] = lastMeaning.split('</Q>')[0];
        
        item.meanings = pieces;
        
        /// checked
        item.checked = Storage.isChecked(item.idx); 
    })

    return data.data;
}


export default instance;