<template>
  <div class="container">
    <h1>Rhyme Search</h1>
    <!-- end title -->
    <!-- search bar -->
    <b-container>
      <b-row>
        <b-col cols="9">
          <b-form-input
            v-on:keyup.native.enter="onSearch"
            v-model="input"
            type="text"
            placeholder="Enter the rhyme. Ex: ost, ast, oup,.."
          />
        </b-col>

        <b-col cols="3">
          <b-form-input type="number" v-model="numWordsOnRow" @change="formatData"></b-form-input>
        </b-col>
      </b-row>
    </b-container><!-- end search bar -->

    <b-container>
       <b-alert v-if="numFound > 0" variant="success" show>Found: {{numFound}}</b-alert>
      <b-row v-for="(chunk, index) in wordChunks" :key="index">
          <Word  v-for="word in chunk" :key="word.idx" :word="word"  />
      </b-row><!-- end loop chunk -->
    </b-container>
  </div>
</template>

<script>
import API from "app/service/api";
import chunk from "lodash/chunk";
import Word from './Word';

let listWords = [];

let lastInput = localStorage.lastRhyme;

export default {
  components: { Word },
  data() {
    return {
      input: lastInput,
      numFound: 0,
      listWords: [],
      wordChunks: [],
      numWordsOnRow: 5
    };
  },
  created(){
      if(this.input) {
          this.onSearch();
      }
  },
  methods: {
    formatData(words = null) {
      if ( typeof words === 'object') { listWords = words };
      this.wordChunks = chunk(listWords, this.numWordsOnRow);
    },
    async onSearch() {
      if (!this.input) {
        return;
      }
      localStorage.lastRhyme = this.input;
      let words = await API.searchByRhyme(this.input);
      this.numFound = words.length;
      this.formatData(words)
    }
  }
};
</script>
<style scoped lang="scss">
.container {
  padding: 10px;
}
</style>